import React from 'react'
import Form from 'react-bootstrap/Form'
import { useHistory } from 'react-router-dom'

function Login() {
    const history = useHistory();

    const openDashboard = () =>{
        history.push('/dashboard')
    }

    return (
        <div className='bg-login'>
            <div className='position-absolute top-0 left-0 w-100 h-100 bg-layer'></div>
            <div className='container'>
                <div className='row'>
                    <div className='col-12 text-center'>
                        <img src="img/logo.png" alt="" className='w-50 mx-auto' />
                    </div>

                    <div className='col-10 mx-auto'>
                        <div className='row'>
                            <div className='col-md-4 text-center'>
                                <img src="img/boy-2.png" alt="" className='w-100' />
                            </div>
                            <div className='col-md-8'>
                                <div className='login-form '>

                                    <div className='login-inner-form'>
                                        <h3 className='mb-4'>Login</h3>
                                        <Form.Group className="mb-3" controlId="exampleForm.ControlInput1">
                                            <Form.Label>Username</Form.Label>
                                            <Form.Control type="text" placeholder="User Name" className='mb-3' />
                                        </Form.Group>
                                        <Form.Group className="mb-3" controlId="exampleForm.ControlInput1">
                                            <Form.Label>Password</Form.Label>
                                            <Form.Control type="password" placeholder="Password" className='mb-3' />
                                        </Form.Group>
                                        <button onClick={openDashboard}>Login</button>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    )
}

export default Login
